.wp-block-create-block-tmy {
	font-size: 64px;
}
.event-single {
	height: 100vh;
	// position: absolute !important;
	// top: 0;
}
.event-single h5 {
	margin: 0;
}
.event-single-wrapper {
	display: grid;
	grid-gap: 10px;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 130px 130px 130px;
	position: sticky;
	top: 0;
	img:not(.events-icon) {
		width: 100%;
		object-fit: cover;
		height: 100%;
	}
}
figure {
	margin: 0;
}
.imagelayout-1 {
	.events-1 {
		grid-column: 1 / 2;
		grid-row: 1;
	}
	.events-2 {
		grid-column: 2 / 4;
		grid-row: 1 / 3;
	}
	.events-3 {
		grid-column: 1;
		grid-row: 2;
	}
	.events-4 {
		grid-column: 1;
		grid-row: 3;
	}
	.events-5 {
		grid-column: 2;
		grid-row: 3;
	}
	.events-6 {
		grid-column: 3;
		grid-row: 3;
	}
}
.imagelayout-2 {
	.events-1 {
		grid-column: 1/ 3;
		grid-row: 1/ 3;
	}
	.events-2 {
		grid-column: 3;
		grid-row: 1 ;
	}
	.events-3 {
		grid-column: 3;
		grid-row: 2;
	}
	.events-4 {
		grid-column: 1;
		grid-row: 3;
	}
	.events-5 {
		grid-column: 1;
		grid-row: 4;
	}
	.events-6 {
		grid-column: 2 / 4;
		grid-row: 3 /5;
	}
}

.hidden {
	opacity: 0;
}
